import * as col from "./ColorsNames";

const colorHexes = new Map ([
    [col.BLUE, "#0074D9"],
    [col.GRAY, "#DDDDDD"],
    [col.GREEN, "#2ECC40"],
    [col.ORANGE, "#FF851B"],
    [col.PINK, "#F012BE"],
    [col.RED, "#FF4136"],
    [col.YELLOW, "#FFDC00"],
]);

export default colorHexes;



